@media screen and (max-width: 767px) {
  .burger__btn {
    width: 24px;
    height: 17px;
    display: block;
    position: relative;
  }

  .burger__btn:before, .burger__btn:after {
    content: "";
    width: 100%;
    height: 3px;
    background-color: var(--icon-menu-cl);
    position: absolute;
    left: 0;
  }

  .burger__btn:before {
    transition-property: transform;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);
    top: 0;
  }

  .burger__btn:after {
    transition-property: transform;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);
    bottom: 0;
  }
}

.burger__btn.active:before {
  top: 7px;
  transform: rotate(45deg);
}

.burger__btn.active:after {
  top: 7px;
  transform: rotate(-45deg);
}

.burger__btn.active .burger__icon {
  opacity: 0;
}

.burger__icon {
  width: 100%;
  height: 3px;
  background-color: var(--icon-menu-cl);
  position: absolute;
  top: 7px;
  left: 0;
}

