.btn {
  // font-size: calc(8.54px + (16 - 8.54) * ((100vw - 769px) / (1440 - 769)));
  line-height: 1.5;
  letter-spacing: 0.06em;
  color: var(--white-txt-cl);
  background-color: #000000;
  border-radius: 13px;
  border: transparent;

  @media screen and (max-width: 1439px) {
    font-size: calc(12px + (16 - 12) * ((100vw - 768px) / (1440 - 768)));
    padding-top: calc(8px + (11 - 8) * ((100vw - 769px) / (1440 - 769)));
    padding-right: calc(14px + (20 - 14) * ((100vw - 769px) / (1440 - 769)));
    padding-bottom: calc(8px + (11 - 8) * ((100vw - 769px) / (1440 - 769)));
    padding-left: calc(14px + (20 - 14) * ((100vw - 769px) / (1440 - 769)));
  }

  @media screen and (min-width: 1440px) {
    font-size: 16px;
    padding-top: 11px;
    padding-right: 20px;
    padding-bottom: 11px;
    padding-left: 20px;
  }

